exports.getData = (callback) => {
  fetch("https://jsonbox.io/box_bdbe89393980ec2873d8", {
    method: "GET",
  })
    .then((res) => res.json())
    .then((data) => {
      //console.log(data);
      return callback(0, data);
    })
    .catch((err) => {
      //console.log(err);
      return callback(1, "Sorry we are currently down");
    });
};
