import React, { useState, useEffect } from "react";
import Base from "./Base";
import { getData } from "./getStory";
import StoryCard from "./Cards";
import { CardGroup } from "react-bootstrap";

const Sayantan = () => {
  const [stories, set_stories] = useState([]);
  const [error, set_error] = useState(false);

  const loadAllStories = () => {
    getData((err, data) => {
      if (err) {
        set_error(error);
      } else {
        set_stories(data);
      }
    });
  };

  useEffect(() => {
    loadAllStories();
  }, []);
  return (
    <Base
      title="Rejection"
      description="REJECTION is the injection to SUCCESS"
      linkDesc="Add Your Story"
      linkURL="https://werfree.github.io/addStory/"
    >
      <CardGroup>
        {stories &&
          stories.map((story, index) => {
            return (
              <div key={index} className="col m-3">
                <StoryCard story={story} />
              </div>
            );
          })}
      </CardGroup>
    </Base>
  );
};
export default Sayantan;
