import React from "react";
import { Card } from "react-bootstrap";

const StoryCard = (story) => {
  const storyDesc = story.story;
  const cardName = storyDesc ? storyDesc.name : "K";
  const cardImage = storyDesc ? storyDesc.photo : "";
  const cardProfession = storyDesc ? storyDesc.profession : "";
  const cardStory = storyDesc ? storyDesc.story : "";
  const cardSchool = storyDesc.school ? storyDesc.school : "";

  return (
    <Card className=" bg-secondary round border">
      <Card.Img
        variant="top"
        src={cardImage}
        style={{
          marginLeft: "auto",
          marginRight: "auto",
          width: "200px",
          height: "150px",
        }}
      />
      <div className="border-bottom  border-warning mt-2"></div>
      <Card.Body>
        <Card.Title className="text-warning">{cardName}</Card.Title>
        <Card.Text>{cardStory}</Card.Text>
      </Card.Body>
      <Card.Footer className="border">
        <small className="text-light">
          {cardProfession} {cardSchool}
        </small>
      </Card.Footer>
    </Card>
  );
};
export default StoryCard;
